/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>It's been sometime since I looked at my own website. As a web developer I should have a site, and really it should have lots of lovely bells and whistles to show off what I can do. But this site is not going to do that.</p>\n<p>Keep things simple. Keep it clean. Keep it elegant.</p>\n<p>I dropped the old and much underused Wordpress install of 2014 and am now opting for the much more elegant static site generation via Gatsby.js. I am also dropping the hot pink I used to use in favor of neutral colours and have adopted an illustration of a dinosaur skeleton. The dinosaur is purely because I like the illustration.</p>\n<p>My current platform is: -</p>\n<ul>\n<li>GitHub Pages Hosting</li>\n<li>Gatsby.js</li>\n</ul>\n<p>That will do for now. The next few updates will include adapting the template to present more information about my company.t. Future blog posts will document development and design work that I do and experiences I have working in the web development field.</p>","frontmatter":{"date":"July 12, 2017","path":"/first-post","tags":["website"],"title":"A new site built on Gatsby"}}},"pathContext":{"prev":false,"next":{"excerpt":"I love Git, its the go-to version and source control platform of choice for all my projects. For a while now I have been meaning to work out how to get tab autocomplete to work for branch names. Having to type them is frustrating. This morning I…","html":"<p>I love Git, its the go-to version and source control platform of choice for all my projects. For a while now I have been meaning to work out how to get tab autocomplete to work for branch names. Having to type them is frustrating. This morning I found out its just a matter of adding a single line to my ~/.bash_profile; thats it. I really wish I had looked this up sooner.</p>\n<p>Add the following to your <code>~/.bash_profile</code> or <code>~/.bashrc:</code></p>\n<pre><code class=\"language-bash\">source /usr/local/git/contrib/completion/git-completion.bash\n</code></pre>\n<p>You will need to restart your terminal or you can just reload the relevant file by entering</p>\n<pre><code class=\"language-bash\">$ source ~/.bash_profile\n</code></pre>\n<p>or</p>\n<pre><code class=\"language-bash\">$ source ~/.bashrc\n</code></pre>\n<p>As appropriate. Happy tabbing :)</p>","id":"/Users/neotoxic/projects/William-Owen.github.io/src/posts/2017-11-29-code_test/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2016-09-02T17:12:33.962Z","path":"/adding-tab-completion-for-git-on-osx","tags":["code","Git"],"title":"Adding tab completion for Git on OSX"}}}}

/***/ })

